.live-item {
    width: 42%;
    padding-top: .4rem;
    padding-left: 1.7rem;

    .input-title {
        height: rem(110);
        padding: .8rem 1rem;
        margin-top: 1.1rem;
        resize: none;

        font-size: 1.4rem;
        font-weight: 600;
        line-height: 1.25;
        letter-spacing: -.062rem;
    }

    .input {
        padding-left: 2.4rem;
        margin-top: 1.3rem;
    }

    .checkbox {
        display: inline-block;
        margin-top: 1.5rem;
        margin-right: .6rem;
    }

    .btn {
        height: 2.6rem;
        margin-top: 1.2rem;
    }
}
