.icon {
    display: inline-block;
    vertical-align: middle;

    &.arrowRight {
        opacity: 0.33;
        margin-right: 4px;
    }

    &-arrowRight-light {
        @include bg($arrowRight-light);
    }

    &_arrowLeft {
        opacity: 0.33;
    }

    &-dropdown {
        @include bg($dropdown-arrow);
    }

    &-okay {
        @include bg($okay);
    }

    &-delete {
        @include bg($delete);

        &:hover {
            @include bg($delete-bold);
        }

        &-reverse {
            @include bg($delete-bold);
        }
    }

    &-delete-lg {
        @include bg($delete-lg);
        &:hover {
            @include bg($delete-lg-active);
        }
    }

    &-go-right {
        @include bg($go-right);

        &:hover {
            @include bg($go-right-active);
        }
    }

    &_logout {
        opacity: 0.59;
        &:hover {
            opacity: 1;
        }
    }

    &-undo-lite {
        @include bg($undo-lite);
    }

    &-question-lite {
        @include bg($question-lite);
    }

    &-resize {
        @include bg($resize);
        &:hover {
            @include bg($resize-active);
        }
    }

    &-rotate {
        @include bg($rotate);
        &:hover {
            @include bg($rotate-active);
        }
    }

    &-crop {
        @include bg($crop);
        &:hover {
            @include bg($crop-active);
        }
    }

    &-text-bold {
        @include bg($text-bold);
        &:hover {
            @include bg($text-bold-active);
        }
    }

    &-text-italic {
        @include bg($text-italic);
        &:hover {
            @include bg($text-italic-active);
        }
    }

    &-image {
        @include bg($image);
    }

    &-images {
        @include bg($images);
    }

    &-text-video {
        @include bg($text-video);
        &:hover {
            @include bg($text-video-active);
        }
    
        &-lg {
            @include bg($text-video-lg);
        }

    }

    &-orph {
        @include bg($orph);
        &:hover {
            @include bg($orph-active);
        }
    }

    &-youtube {
        @include bg($youtube);
    }

    &-twitter {
        @include bg($twitter);
    }

    &-facebook {
        @include bg($facebook);
    }

    // иконки со страницы статистики
    &-article {
        @include bg($article);
    }

    &-video {
        @include bg($video);
    }

    &-programms {
        @include bg($programms);
    }

    &-summary {
        @include bg($summary);
    }

    // иконки в левом вертикальном меню
    &-logo {
        @include bg($logo);
    }

    &-logo-light {
        @include bg($logo-light);
    }

    &-feed {
        @include bg($feed);
    }

    &-view {
        @include bg($view);
    }

    &-draft {
        @include bg($draft);
    }

    &-ready {
        @include bg($ready);
    }

    &-main {
        @include bg($main);
    }

    &-tv {
        @include bg($tv);
    }

    &-live {
        @include bg($live);
    }

    // source
    &-tass {
        @include bg($tass);
    }

    &-live-on {
        @include bg($live-on);
    }

    &-live-off {
        @include bg($live-off);
    }

}

// если активный пункт меню
.is-active {
    .icon {
        &-feed {
            @include bg($feed-active);
        }

        &-view {
            @include bg($view-active);
        }

        &-draft {
            @include bg($draft-active);
        }

        &-ready {
            @include bg($ready-active);
        }

        &-main {
            @include bg($main-active);
        }

        &-tv {
            @include bg($tv-active);
        }

        &-live {
            @include bg($live-active);
        }
    }
}

.btn:hover {
    .icon-arrowRight-light {
        @include bg($arrowRight-light-hover);
        opacity: 0.33;
    }
}